import { Routes, Route } from "react-router-dom";
import Button from "./Button";
import Screen from "./Screen";
import "./styles.css";

/* Change debug to `true` to see the invisible Buttons */
export const debug = false;

/* Step 1
<---- UPLOAD YOU OWN IMAGE FILES INTO /public

You can use .jpg, .png, .svg
Try to make all your images roughly the same width, 
i.e. 360 pixels wide for a mobile screen
or 1200 pixels wide for a desktop screen
*/

export default function App() {
  /* Step 2

  For each Screen in your click dummy, create a new
  - Route
    - Screen

  If you want to make the screens interactive, place a Button inside the screen
  – Route
    – Screen
      – Button
      – Button

  The Button "to" parameter links to the Route "path" parameter.
  The Screen "image" parameter should be the file name in the / folder (root)
  */
  return (
    <div className="App">
      <Routes>
        <Route
          path="/"
          element={
            <Screen image="/Ramp-Up.jpg">
              <Button to="/login" x="345" y="225" w="108" h="100" />
            </Screen>
          }
        />
        <Route
          path="/login"
          element={
            <Screen image="/log-in-page.jpg">
              <Button to="/home" x="370" y="300" w="90" h="30" />
            </Screen>
          }
        />
        <Route
          path="/home"
          element={
            <Screen image="/homepage.jpg">
              <Button to="/search-results" x="70" y="35" w="50" h="55" />
            </Screen>
          }
        />
        <Route
          path="/search-results"
          element={
            <Screen image="/search-results.jpg">
              <Button to="/view" x="85" y="120" w="70" h="75" />
            </Screen>
          }
        />
        <Route
          path="/view"
          element={
            <Screen image="/view.jpg">
              <Button to="/search-results" x="80" y="50" w="30" h="30" />
            </Screen>
          }
        />
      </Routes>
    </div>
  );
}
